$color_1: #1d1d10;
$color_2: red;
$color_3: inherit;
$font-family_1: et-book, Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif;
$font-family_2: et-book-roman-old-style;
$font-family_3: "Trebuchet MS", "Gill Sans", "Gill Sans MT", sans-serif;
$font-family_4: "Gill Sans", "Gill Sans MT", Calibri, sans-serif;
$font-family_5: Consolas, "Liberation Mono", Menlo, Courier, monospace;
$background-color_1: #b9aec3;
$background-color_2: rgba(184, 173, 194, 0.0);

/* Import ET Book styles
   adapted from https://github.com/edwardtufte/et-book/blob/gh-pages/et-book.css */
@font-face {
	font-family: "et-book";
	src: url("/fonts/et-book/et-book-roman-line-figures/et-book-roman-line-figures.eot");
	src: url("/fonts/et-book/et-book-roman-line-figures/et-book-roman-line-figures.eot?#iefix") format("embedded-opentype"), url("et-book/et-book-roman-line-figures/et-book-roman-line-figures.woff") format("woff"), url("et-book/et-book-roman-line-figures/et-book-roman-line-figures.ttf") format("truetype"), url("et-book/et-book-roman-line-figures/et-book-roman-line-figures.svg#etbookromanosf") format("svg");
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}
@font-face {
	font-family: "et-book";
	src: url("/fonts/et-book/et-book-display-italic-old-style-figures/et-book-display-italic-old-style-figures.eot");
	src: url("/fonts/et-book/et-book-display-italic-old-style-figures/et-book-display-italic-old-style-figures.eot?#iefix") format("embedded-opentype"), url("et-book/et-book-display-italic-old-style-figures/et-book-display-italic-old-style-figures.woff") format("woff"), url("et-book/et-book-display-italic-old-style-figures/et-book-display-italic-old-style-figures.ttf") format("truetype"), url("et-book/et-book-display-italic-old-style-figures/et-book-display-italic-old-style-figures.svg#etbookromanosf") format("svg");
	font-weight: normal;
	font-style: italic;
	font-display: swap;
}
@font-face {
	font-family: "et-book";
	src: url("/fonts/et-book/et-book-bold-line-figures/et-book-bold-line-figures.eot");
	src: url("/fonts/et-book/et-book-bold-line-figures/et-book-bold-line-figures.eot?#iefix") format("embedded-opentype"), url("et-book/et-book-bold-line-figures/et-book-bold-line-figures.woff") format("woff"), url("et-book/et-book-bold-line-figures/et-book-bold-line-figures.ttf") format("truetype"), url("et-book/et-book-bold-line-figures/et-book-bold-line-figures.svg#etbookromanosf") format("svg");
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}
@font-face {
	font-family: "et-book-roman-old-style";
	src: url("/fonts/et-book/et-book-roman-old-style-figures/et-book-roman-old-style-figures.eot");
	src: url("/fonts/et-book/et-book-roman-old-style-figures/et-book-roman-old-style-figures.eot?#iefix") format("embedded-opentype"), url("et-book/et-book-roman-old-style-figures/et-book-roman-old-style-figures.woff") format("woff"), url("et-book/et-book-roman-old-style-figures/et-book-roman-old-style-figures.ttf") format("truetype"), url("et-book/et-book-roman-old-style-figures/et-book-roman-old-style-figures.svg#etbookromanosf") format("svg");
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}
/* Tufte CSS styles */
/* Adds dark mode */
/* Chapter Epigraphs */
/* end chapter epigraphs styles */
/* 50 + 5 == 55, to be the same width as paragraph */
/* Links: replicate underline that clears descenders */
/* Adds dark mode */
/* Sidenotes, margin notes, figures, captions */
html {
	font-size: 15px;
}
body {
	width: 87.5%;
	margin-left: auto;
	margin-right: auto;
	padding-left: 12.5%;
	font-family: $font-family_1;
	background-color: $background-color_1;
	color: $color_1;
	max-width: 1400px;
	counter-reset: sidenote-counter;
}
h1 {
	font-weight: 400;
	margin-top: 4rem;
	margin-bottom: 1.5rem;
	font-size: 3.2rem;
	line-height: 1;
	>code {
		font-size: 0.80em;
	}
}
h2 {
	font-style: italic;
	font-weight: 400;
	margin-top: 2.1rem;
	margin-bottom: 1.4rem;
	font-size: 2.2rem;
	line-height: 1;
	>code {
		font-size: 0.80em;
	}
}
h3 {
	font-style: italic;
	font-weight: 400;
	font-size: 1.7rem;
	margin-top: 2rem;
	margin-bottom: 1.4rem;
	line-height: 1;
	>code {
		font-size: 0.80em;
	}
}
hr {
	height: 1px;
	width: 55%;
	border: 0;
	border-top: 1px solid #ccc;
	margin: 1em 0;
	padding: 0;
}
p.subtitle {
	font-style: italic;
	margin-top: 1rem;
	margin-bottom: 1rem;
	font-size: 1.8rem;
	display: block;
	line-height: 1;
}
.numeral {
	font-family: $font-family_2;
}
.danger {
	color: $color_2;
}
article {
	padding: 5rem 0rem;
}
section {
	padding-top: 1rem;
	padding-bottom: 1rem;
	>p {
		width: 55%;
	}
	>footer {
		width: 55%;
	}
	>table {
		width: 55%;
	}
	>dl {
		width: 50%;
		-webkit-padding-start: 5%;
	}
	>ol {
		width: 50%;
		-webkit-padding-start: 5%;
	}
	>ul {
		width: 50%;
		-webkit-padding-start: 5%;
	}
}
p {
	font-size: 1.4rem;
	line-height: 2rem;
	margin-top: 1.4rem;
	margin-bottom: 1.4rem;
	padding-right: 0;
	vertical-align: baseline;
}
dl {
	font-size: 1.4rem;
	line-height: 2rem;
}
ol {
	font-size: 1.4rem;
	line-height: 2rem;
}
ul {
	font-size: 1.4rem;
	line-height: 2rem;
}
div.epigraph {
	margin: 5em 0;
	>blockquote {
		margin-top: 3em;
		margin-bottom: 3em;
		font-style: italic;
		>p {
			font-style: italic;
		}
		>footer {
			font-style: normal;
			>cite {
				font-style: italic;
			}
		}
	}
}
blockquote {
	font-size: 1.4rem;
	p {
		width: 55%;
		margin-right: 40px;
	}
	footer {
		width: 55%;
		font-size: 1.1rem;
		text-align: right;
	}
	.sidenote {
		margin-right: -82%;
		min-width: 59%;
		text-align: left;
	}
	.marginnote {
		margin-right: -82%;
		min-width: 59%;
		text-align: left;
	}
}
dt {
	&:not(:first-child) {
		margin-top: 0.25rem;
	}
}
li {
	&:not(:first-child) {
		margin-top: 0.25rem;
	}
}
.sitelogo {
	max-width: 10.5%;
	margin: 0 auto;
}
figure {
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	max-width: 55%;
	-webkit-margin-start: 0;
	-webkit-margin-end: 0;
	margin: 0 0 3em 0;
}
figcaption {
	float: right;
	clear: right;
	margin-top: 0;
	margin-bottom: 0;
	font-size: 1.1rem;
	line-height: 1.6;
	vertical-align: baseline;
	position: relative;
	max-width: 40%;
}
figure.fullwidth {
	figcaption {
		margin-right: 24%;
	}
}
a {
	&:link {
		color: $color_3;
		text-decoration: none;
		background: -webkit-linear-gradient(#fffff8, #fffff8), -webkit-linear-gradient(#fffff8, #fffff8), -webkit-linear-gradient(currentColor, currentColor);
		background: linear-gradient(#fffff8, #fffff8), linear-gradient(#fffff8, #fffff8), linear-gradient(currentColor, currentColor);
		-webkit-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
		-moz-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
		background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
		background-repeat: no-repeat, no-repeat, repeat-x;
		text-shadow: 0.03em 0 #fffff8, -0.03em 0 #fffff8, 0 0.03em #fffff8, 0 -0.03em #fffff8, 0.06em 0 #fffff8, -0.06em 0 #fffff8, 0.09em 0 #fffff8, -0.09em 0 #fffff8, 0.12em 0 #fffff8, -0.12em 0 #fffff8, 0.15em 0 #fffff8, -0.15em 0 #fffff8;
		background-position: 0% 93%, 100% 93%, 0% 93%;
		&::selection {
			text-shadow: 0.03em 0 #b4d5fe, -0.03em 0 #b4d5fe, 0 0.03em #b4d5fe, 0 -0.03em #b4d5fe, 0.06em 0 #b4d5fe, -0.06em 0 #b4d5fe, 0.09em 0 #b4d5fe, -0.09em 0 #b4d5fe, 0.12em 0 #b4d5fe, -0.12em 0 #b4d5fe, 0.15em 0 #b4d5fe, -0.15em 0 #b4d5fe;
			background: #b4d5fe;
		}
		&::-moz-selection {
			text-shadow: 0.03em 0 #b4d5fe, -0.03em 0 #b4d5fe, 0 0.03em #b4d5fe, 0 -0.03em #b4d5fe, 0.06em 0 #b4d5fe, -0.06em 0 #b4d5fe, 0.09em 0 #b4d5fe, -0.09em 0 #b4d5fe, 0.12em 0 #b4d5fe, -0.12em 0 #b4d5fe, 0.15em 0 #b4d5fe, -0.15em 0 #b4d5fe;
			background: #b4d5fe;
		}
	}
	&:visited {
		color: $color_3;
	}
}
.no-tufte-underline {
	&:link {
		background: unset;
		text-shadow: unset;
	}
}
.tufte-underline {
	text-decoration: none;
	background: -webkit-linear-gradient(#fffff8, #fffff8), -webkit-linear-gradient(#fffff8, #fffff8), -webkit-linear-gradient(currentColor, currentColor);
	background: linear-gradient(#fffff8, #fffff8), linear-gradient(#fffff8, #fffff8), linear-gradient(currentColor, currentColor);
	-webkit-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
	-moz-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
	background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
	background-repeat: no-repeat, no-repeat, repeat-x;
	text-shadow: 0.03em 0 #fffff8, -0.03em 0 #fffff8, 0 0.03em #fffff8, 0 -0.03em #fffff8, 0.06em 0 #fffff8, -0.06em 0 #fffff8, 0.09em 0 #fffff8, -0.09em 0 #fffff8, 0.12em 0 #fffff8, -0.12em 0 #fffff8, 0.15em 0 #fffff8, -0.15em 0 #fffff8;
	background-position: 0% 93%, 100% 93%, 0% 93%;
}
.hover-tufte-underline {
	&:hover {
		text-decoration: none;
		background: -webkit-linear-gradient(#fffff8, #fffff8), -webkit-linear-gradient(#fffff8, #fffff8), -webkit-linear-gradient(currentColor, currentColor);
		background: linear-gradient(#fffff8, #fffff8), linear-gradient(#fffff8, #fffff8), linear-gradient(currentColor, currentColor);
		-webkit-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
		-moz-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
		background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
		background-repeat: no-repeat, no-repeat, repeat-x;
		text-shadow: 0.03em 0 #fffff8, -0.03em 0 #fffff8, 0 0.03em #fffff8, 0 -0.03em #fffff8, 0.06em 0 #fffff8, -0.06em 0 #fffff8, 0.09em 0 #fffff8, -0.09em 0 #fffff8, 0.12em 0 #fffff8, -0.12em 0 #fffff8, 0.15em 0 #fffff8, -0.15em 0 #fffff8;
		background-position: 0% 93%, 100% 93%, 0% 93%;
	}
}
img {
	max-width: 100%;
}
.sidenote {
	float: right;
	clear: right;
	margin-right: -60%;
	width: 50%;
	margin-top: 0.3rem;
	margin-bottom: 0;
	font-size: 1.1rem;
	line-height: 1.3;
	vertical-align: baseline;
	position: relative;
	&:before {
		font-family: $font-family_2;
		position: relative;
		vertical-align: baseline;
		content: counter(sidenote-counter) " ";
		font-size: 1rem;
		top: -0.5rem;
	}
	>code {
		font-size: 1rem;
	}
}
.marginnote {
	float: right;
	clear: right;
	margin-right: -60%;
	width: 50%;
	margin-top: 0.3rem;
	margin-bottom: 0;
	font-size: 1.1rem;
	line-height: 1.3;
	vertical-align: baseline;
	position: relative;
	>code {
		font-size: 1rem;
	}
}
.sidenote-number {
	counter-increment: sidenote-counter;
	&:after {
		font-family: $font-family_2;
		position: relative;
		vertical-align: baseline;
		content: counter(sidenote-counter);
		font-size: 1rem;
		top: -0.5rem;
		left: 0.1rem;
	}
}
div.fullwidth {
	width: 100%;
}
table.fullwidth {
	width: 100%;
}
div.table-wrapper {
	overflow-x: auto;
	font-family: $font-family_3;
}
.sans {
	font-family: $font-family_4;
	letter-spacing: .03em;
	>code {
		font-size: 1.2rem;
	}
}
code {
	font-family: $font-family_5;
	font-size: 1.0rem;
	line-height: 1.42;
	-webkit-text-size-adjust: 100%;
}
pre {
	>code {
		font-family: $font-family_5;
		font-size: 1.0rem;
		line-height: 1.42;
		-webkit-text-size-adjust: 100%;
		font-size: 0.9rem;
		width: 52.5%;
		margin-left: 2.5%;
		overflow-x: auto;
		display: block;
	}
}
pre.fullwidth {
	>code {
		width: 90%;
	}
}
.fullwidth {
	max-width: 90%;
	clear: both;
}
span.newthought {
	font-variant: small-caps;
	font-size: 1.2em;
}
input.margin-toggle {
	display: none;
}
label.sidenote-number {
	display: inline-block;
	max-height: 2rem;
}
label.margin-toggle {
	&:not(.sidenote-number) {
		display: none;
	}
}
.iframe-wrapper {
	position: relative;
	padding-bottom: 56.25%;
	padding-top: 25px;
	height: 0;
	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
.site-button {
	display: flex;
	cursor: crosshair;
	margin: 0 auto;
	outline: none;
	border: none;
	background-color: $background-color_2;
	img { margin: 0 0;
	border-radius: 10px;
}
	.site-button {
		&:hover {
			transform: rotate(5deg);
		}
	}
}
.other-buttons {
	&:hover {
		transform: rotate(5deg);
	}
	cursor: crosshair;
	outline: none;
	border: none;
	background-color: $background-color_2;
	img { display: block;
	margin: 0 0;
}
.row {
	display: flex;
	width: auto;
	margin: 0 auto;
	justify-content: center;
}
@media (prefers-color-scheme: dark) {
	body {
		background-color: $background-color_1;
		color: $color_1;
	}
	a {
		&:link {
			text-shadow: 0.03em 0 #151515, -0.03em 0 #151515, 0 0.03em #151515, 0 -0.03em #151515, 0.06em 0 #151515, -0.06em 0 #151515, 0.09em 0 #151515, -0.09em 0 #151515, 0.12em 0 #151515, -0.12em 0 #151515, 0.15em 0 #151515, -0.15em 0 #151515;
		}
	}
	.tufte-underline {
		text-shadow: 0.03em 0 #151515, -0.03em 0 #151515, 0 0.03em #151515, 0 -0.03em #151515, 0.06em 0 #151515, -0.06em 0 #151515, 0.09em 0 #151515, -0.09em 0 #151515, 0.12em 0 #151515, -0.12em 0 #151515, 0.15em 0 #151515, -0.15em 0 #151515;
	}
	.hover-tufte-underline {
		&:hover {
			text-shadow: 0.03em 0 #151515, -0.03em 0 #151515, 0 0.03em #151515, 0 -0.03em #151515, 0.06em 0 #151515, -0.06em 0 #151515, 0.09em 0 #151515, -0.09em 0 #151515, 0.12em 0 #151515, -0.12em 0 #151515, 0.15em 0 #151515, -0.15em 0 #151515;
		}
	}
}
@media screen and (-webkit-min-device-pixel-ratio: 0) {
	a {
		&:link {
			background-position-y: 87%, 87%, 87%;
		}
	}
	.tufte-underline {
		background-position-y: 87%, 87%, 87%;
	}
	.hover-tufte-underline {
		&:hover {
			background-position-y: 87%, 87%, 87%;
		}
	}
}
@media (max-width: 760px) {
	body {
		width: 84%;
		padding-left: 8%;
		padding-right: 8%;
	}
	hr {
		width: 100%;
	}
	section {
		>p {
			width: 100%;
		}
		>footer {
			width: 100%;
		}
		>table {
			width: 100%;
		}
		>dl {
			width: 90%;
		}
		>ol {
			width: 90%;
		}
		>ul {
			width: 90%;
		}
	}
	pre {
		>code {
			width: 97%;
		}
	}
	figure {
		max-width: 90%;
	}
	figcaption {
		margin-right: 0%;
		max-width: none;
	}
	figure.fullwidth {
		figcaption {
			margin-right: 0%;
			max-width: none;
		}
	}
	blockquote {
		margin-left: 1.5em;
		margin-right: 0em;
		p {
			width: 100%;
		}
		footer {
			width: 100%;
		}
	}
	label.margin-toggle {
		&:not(.sidenote-number) {
			display: inline;
		}
	}
	.sidenote {
		display: none;
	}
	.marginnote {
		display: none;
	}
	.margin-toggle {
		&:checked {
			+ {
				.sidenote {
					display: block;
					float: left;
					left: 1rem;
					clear: both;
					width: 95%;
					margin: 1rem 2.5%;
					vertical-align: baseline;
					position: relative;
				}
				.marginnote {
					display: block;
					float: left;
					left: 1rem;
					clear: both;
					width: 95%;
					margin: 1rem 2.5%;
					vertical-align: baseline;
					position: relative;
				}
			}
		}
	}
	label {
		cursor: pointer;
	}
	div.table-wrapper {
		width: 85%;
	}
	table {
		width: 85%;
	}
	img {
		width: 100%;
	}
}
}